/*
    font face
 */

 @font-face {
    font-family: "Bebas Neue";
    src: url(../fonts/BebasNeueBold.woff);
    font-weight: 600;
  }
  @font-face {
    font-family: "Bebas Neue";
    src: url(../fonts/BebasNeueRegular.woff);
    font-weight: 400;
  }
  @font-face {
    font-family: "Roboto";
    src: url(../fonts/Roboto-Regular.woff);
    font-weight: 400;
  }
  @font-face {
    font-family: "Roboto";
    src: url(../fonts/Roboto-Medium.woff);
    font-weight: 600;
  }


  $font : "Roboto";
  $font-title : "Bebas Neue";


